<template>
	<view>
		<template>
			<view style="width: 400px;height: 800px;" id="myMapView" />
		</template>
	</view>
</template>

<!-- 逻辑层代码 -->
<script>
	export default {
		data() {
			return {}
		},
		methods: {}
	}
</script>

<!-- RenderJS视图层代码 -->
<script module="myMapViews" lang="renderjs">
	import {
		loadModules
	} from 'esri-loader'
	export default {
		name: 'myMapView',
		data() {
			return {
				myMapObject: {}, // 对象，存储关于地图的图层、方法、属性等
			}
		},
		methods: {
			createMapView() {
				const options = {
					url: 'https://js.arcgis.com/4.14/init.js',
					css: 'https://js.arcgis.com/4.14/esri/themes/light/main.css'
				};
				loadModules([
					"esri/Map",
					"esri/views/MapView",
					"esri/Basemap",
					"esri/layers/Layer",
					"esri/layers/TileLayer",
				], options).then(([Map, MapView, Basemap, Layer, TileLayer]) => {

					// 地图的底图
					var url =
						"http://cache1.arcgisonline.cn/arcgis/rest/services/ChinaOnlineCommunity_Mobile/MapServer";
					var basemaplayer = new TileLayer({
						url: url,
						visible: true,
					});

					// 地图对象
					var map = new Map({
						basemap: "", // 底图置空
						layers: [basemaplayer], // 添加自定义的layer为底图
					});

					// 创建一个mapView并绑定页面元素
					var view = new MapView({
						container: "myMapView",
						map: map,
						zoom: 1, // 缩放比例 值越大图越大
					});

					this.myMapObject.map = map;
				})
			},
		},
		mounted() {
			// 页面初始化完成后
			this.createMapView();
		},
	}
</script>

<style>
